import React, { Component } from 'react';
import {Switch, Route, NavLink, Redirect } from 'react-router-dom';
import AuthorizedRoute from './../utils/AuthorizedRoute'
import './../css/nav.css';
import Index from "./../view/index";
import User from "./../view/user";
import Category from "./../view/category";
import Find from "./../view/find";

class Pulic extends Component {
    componentDidMount() {
    }

    render() {
        return (
            <div className="Public">
                <nav>
                    <NavLink exact to="/index" activeClassName="active"><i className={'icon iconfont icon-tubiao-'}></i><span>首页</span></NavLink>
                    <NavLink to="/category" activeClassName="active"><i className={'icon iconfont icon-fenlei'}></i><span>分类</span></NavLink>
                    <NavLink to="/find" activeClassName="active"><i className={'icon iconfont icon-faxian'}></i><span>发现</span></NavLink>
                    <NavLink to="/user" activeClassName="active"><i className={'icon iconfont icon-tubiaolunkuo-'}></i><span>我的</span></NavLink>
                </nav>
                <Switch>
                    <Route path="/index" component={Index}/>
                    <Route path="/category" component={Category}/>
                    <Route path="/find" component={Find}/>
                    <AuthorizedRoute path="/user" component={User}/>
                    <Redirect from="*" to="/index"/>
                </Switch>
            </div>
        );
    }
}

export default Pulic;


