import React, { Component } from 'react';
import $ from 'jquery';
import './../css/login.css'

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            password: '',
            error: ''
        };

        this.nameChange = this.nameChange.bind(this);
        this.passwordChange = this.passwordChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    nameChange(event) {
        this.setState({name: event.target.value});
    }
    passwordChange(event) {
        this.setState({password: event.target.value});
    }

    error(){
        $('.Login .error').addClass('showerror');
        setTimeout(function(){
            $('.Login .error').removeClass('showerror');
        }, 2000);
    }

    handleSubmit(event) {
        if(this.state.name === ''){
            this.setState({error: '账号不能为空!'})
            this.error()
        }else {
            if(this.state.password === ''){
                this.setState({error: '密码不能为空!'})
                this.error()
            }else {
                if(this.state.name === 'admin' && this.state.password === 'admin'){
                    localStorage.setItem('account', this.state.name);
                    this.props.history.push('user')
                }else {
                    this.setState({error: '账号或密码错误!'})
                    this.error()
                }
            }
        }
        event.preventDefault();
    }

    componentDidMount() {
        fetch("https://www.zdomo.com/wap/api/GetBanner", {method: "POST"})
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result)
                }
            )
    }

    back(){
        window.history.go(-1);
    }

    render() {
        return (
            <div className={'Login'}>
                <div className={'login_header'}>
                    <a className={'go-back'} onClick={this.back}><i className={'icon iconfont icon-fanhui'}></i></a>
                    账号登录
                </div>
                <div className={'login_area'}>
                    <form onSubmit={this.handleSubmit}>
                        <div className={'loginbox'}>
                            <div className={'item'}>
                                <div className={'item_account'}>
                                    <input autoComplete={'off'} type={'text'} name={'user'} placeholder={'账号:admin'} value={this.state.name} onChange={this.nameChange}/>
                                </div>
                            </div>
                            <div className={'item'}>
                                <div className={'item_account pwd'}>
                                    <input autoComplete={'off'} type={'password'} name={'password'} placeholder={'密码:admin'} value={this.state.password} onChange={this.passwordChange}/>
                                    <a className={'iforgetPwd'}>忘记密码</a>
                                </div>
                            </div>
                            <div className={'item'}>
                                <input className={'btnlogin'} type={'submit'} value={'登 录'}/>
                            </div>
                        </div>
                    </form>
                    <div className={'other_login'}>
                        <p>或</p>
                        <div className={'phone_login'}>
                            <a>手机快捷登录</a>
                        </div>
                    </div>
                </div>
                <div className={'error'}><span>{this.state.error}</span></div>
                <div className={'login_footer'}>
                    <em>还没有账号？</em>
                    <a>立即注册</a>
                </div>
            </div>
        );
    }
}

export default Login;


