import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import $ from 'jquery';
import Swiper from 'swiper';
import './../../node_modules/swiper/dist/css/swiper.min.css'
import './../css/home.css';

class home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: [
                'https://cdn.cnbj0.fds.api.mi-img.com/b2c-mimall-media/fc405ca1168e67d097b57352e746d329.jpg?thumb=1&w=720&h=360',
                'https://cdn.cnbj0.fds.api.mi-img.com/b2c-mimall-media/8b59a72fc0f36973a2c1ee01e155a19d.jpg?thumb=1&w=720&h=360',
                'https://cdn.cnbj0.fds.api.mi-img.com/b2c-mimall-media/73446f60fc10330e5a15bee356093253.jpg?thumb=1&w=720&h=360',
            ]
        };
    }

    componentDidMount() {
        setTimeout(function () {
            $(".lodding").remove();
        }, 1000);
        this.swiper();
    }

    swiper() {
        new Swiper('.swiper-container', {
            autoplay: 4000,
            autoplayDisableOnInteraction : false,
            loop : true,
            pagination : '.swiper-pagination',
        })
    }

    render() {
        const banner = this.state.img.map((item) =>
            <div className={'swiper-slide'} key={item}><img src={item} width="100%" alt=""/></div>
        );
        return (
            <div className={'home'}>
                <p className={'lodding'}>
                    <i className={'icon iconfont icon-jiazaizhong'}></i><span>数据加载中...</span>
                </p>
                <div className={'swiper-container'}>
                    <div className={'swiper-wrapper'}>
                        {banner}
                    </div>
                    <div className={'swiper-pagination'}></div>
                </div>
                <div className={'home_menu'}>
                    <Link to=""><i></i><span>菜单一</span></Link>
                    <Link to=""><i></i><span>菜单二</span></Link>
                    <Link to=""><i></i><span>菜单三</span></Link>
                    <Link to=""><i></i><span>菜单四</span></Link>
                    <Link to=""><i></i><span>菜单五</span></Link>
                </div>
            </div>
        );
    }
}

export default home;


