import React, { Component } from 'react';
import $ from 'jquery';
import NProgress from 'nprogress';
import './../../node_modules/nprogress/nprogress.css';
import Home from "./../view/home";
import Movie from "./../view/movie";
import './../css/index.css';

class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: 'https://cdn.cnbj0.fds.api.mi-img.com/b2c-mimall-media/fc405ca1168e67d097b57352e746d329.jpg?thumb=1&w=720&h=360',
            movie: []
        };
    }

    componentDidMount() {
        NProgress.start();
        setTimeout(function () {
            NProgress.done();
        }, 1000);
        this.tabs()
        // fetch("http://www.zdomo.com/wap/api/GetBanner", {method: "POST"})
        // .then(res => res.json())
        // .then(
        //     (result) => {
        //         console.log(result)
        //     }
        // )
    }

    tabs() {
        const that = this;
        $('.tab_con .tab_list').eq(0).show();
        $('.tab_nav .tab_item').click( function () {
            var zindex = $(this).index();
            if (zindex === 1) {
                that.setState({
                    movie: ['./../view/movie']
                })
            }
            $('.tab_con .tab_list').eq(zindex).show().siblings().hide();

            $(this).addClass('active').siblings().removeClass('active');
            if (zindex > 2) {
                var scrollLeft = $('.tab_nav .tab_item').eq(zindex).offset().left + $('.tab_nav').scrollLeft();
                var w = $('.tab_nav .tab_item').eq(zindex - 1).innerWidth();
                $('.tab_nav').scrollLeft(scrollLeft - w);
            } else {
                $('.tab_nav').scrollLeft(0)
            }
        });
    }

    render() {
        return (
            <div className={'index'}>
                <div className={'top'}>
                    <div className={'header'}>
                        <div className={'item'}>
                            <div className={'logo'}>logo</div>
                        </div>
                        <div className={'item'}>
                            <div className={'search'}>
                                <i className={'icon iconfont icon-sousuo'}></i>
                                <input placeholder="搜索商品名称"/>
                            </div>
                        </div>
                        <div className={'item'}>
                            <div className={'history'}><i className={'icon iconfont icon-lishi'}></i></div>
                        </div>
                    </div>
                    <div className={'tab_nav'}>
                        <div className={'tab_item active'}><span>推荐</span></div>
                        <div className={'tab_item'}><span>电影</span></div>
                        <div className={'tab_item'}><span>娱乐</span></div>
                        <div className={'tab_item'}><span>电视直播</span></div>
                        <div className={'tab_item'}><span>小视频</span></div>
                        <div className={'tab_item'}><span>拍电影</span></div>
                        <div className={'tab_item'}><span>搞笑</span></div>
                        <div className={'tab_item'}><span>音乐</span></div>
                    </div>
                </div>
                <div className={'tabs'}>
                    <div className={'tab_con'}>
                        <div className={'tab_list'}>
                            <Home/>
                        </div>
                        <div className={'tab_list'} style={{display: 'none'}}>
                            {
                                this.state.movie.map((item) => {
                                    return <Movie path={item} key={item}></Movie>
                                })
                            }
                        </div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>娱乐</div></div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>电视直播</div></div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>小视频</div></div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>拍电影</div></div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>搞笑</div></div>
                        <div className={'tab_list'} style={{display: 'none'}}><div className={'movie'}>音乐</div></div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Index;


