import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NProgress from 'nprogress';
import './../../node_modules/nprogress/nprogress.css';
import './../css/find.css';

class find extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: 'https://cdn.cnbj0.fds.api.mi-img.com/b2c-mimall-media/5ebd798d6870d96c777a3defbfb1c66b.jpg?thumb=1&w=720&h=360'
        };
    }

    componentDidMount() {
        NProgress.start();
        setTimeout(function () {
            NProgress.done();
        }, 1000);
    }

    render() {
        return (
            <div className={'find'}>
                <div className={'list'}>
                    <Link to="">
                        <div className={'img'}>
                            <img className={'big'} src={this.state.img} alt={''}/>
                        </div>
                        <div className={'info'}>
                            <div className={'l1'}>
                                <div className={'name'}>小米盒子4c</div>
                                <div className={'price'}>￥249</div>
                            </div>
                            <div className={'brief'}>第4代小米盒子储存，体验双升级</div>
                        </div>
                    </Link>
                </div>
                <div className={'list'}>
                    <Link to="">
                        <div className={'img'}>
                            <img className={'big'} src={this.state.img} alt={''}/>
                        </div>
                        <div className={'info'}>
                            <div className={'l1'}>
                                <div className={'name'}>小米盒子4c</div>
                                <div className={'price'}>￥249</div>
                            </div>
                            <div className={'brief'}>第4代小米盒子储存，体验双升级</div>
                        </div>
                    </Link>
                </div>
                <div className={'list'}>
                    <Link to="">
                        <div className={'img'}>
                            <img className={'big'} src={this.state.img} alt={''}/>
                        </div>
                        <div className={'info'}>
                            <div className={'l1'}>
                                <div className={'name'}>小米盒子4c</div>
                                <div className={'price'}>￥249</div>
                            </div>
                            <div className={'brief'}>第4代小米盒子储存，体验双升级</div>
                        </div>
                    </Link>
                </div>
            </div>
        );
    }
}

export default find;


