import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NProgress from 'nprogress';
import './../../node_modules/nprogress/nprogress.css';
import './../css/user.css';

class user extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    componentDidMount() {
        NProgress.start();
        setTimeout(function () {
            NProgress.done();
        }, 1000);
    }

    signout(){
        localStorage.setItem('account', '');
        window.history.go(0);
    }

    render() {
        return (
            <div className={'user'}>
                <header>
                    <div className={'img'}></div>
                    <div className={'name'}>A_ndy°，你好！</div>
                </header>
                <div className={'b1'}>
                    <div className={'order'}>我的订单</div>
                    <div className={'all_order'}>全部订单<i className={'icon iconfont icon-arrow'}></i></div>
                </div>
                <div className={'b2'}>
                    <Link to=""><i className={'icon iconfont icon-icon-test1'}></i><span>待付款</span></Link>
                    <Link to=""><i className={'icon iconfont icon-daishouhuo'}></i><span>待收货</span></Link>
                    <Link to=""><i className={'icon iconfont icon-icon-test'}></i><span>退换修</span></Link>
                </div>
                <ul className={'items'}>
                    <li>
                        <i className={'icon iconfont icon-fuwuzhongxin'}></i>
                        <div className={'item'}>
                            <div className={'tt'}>服务中心</div>
                            <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                        </div>
                    </li>
                    <li>
                        <i className={'icon iconfont icon-wodeyouhui'}></i>
                        <div className={'item'}>
                            <div className={'tt'}>我的优惠</div>
                            <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                        </div>
                    </li>
                </ul>
                <div className={'ortheritem'}>
                    <div className={'tt'}><i className={'icon iconfont icon-shezhi'}></i><span>设置</span></div>
                    <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                </div>
                <a className={'signout'} onClick={this.signout}>退出登录</a>
            </div>
        );
    }
}

export default user;


